// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import 'normalize.css'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import VueWechatTitle from 'vue-wechat-title'
// Vue.use(VueAwesomeSwiper, /* { default global options } */)

const swiperOptions = {
  /* eslint-disable */
  direction: 'vertical',    //horizontal
  slidesPerView: 'auto',    //number 1
  clickable: true,          //false 
  autoplay: false,          //false  
  mousewheel: true,         //false Swiper
  freeMode: true,           //falseslidewrappertruefreeslideautoHeight: true, //truewrappercontainerslide
  autoHeight: true,         //truewrappercontainerslide
  roundLengths: true,       //(border)
  observer: true,           //swiper/swiperswiper
  observeParents: true      //Swiperwindow.resizeSwiper
  /* eslint-enable */
}

Vue.use(VueWechatTitle)
Vue.use(VueAwesomeSwiper, swiperOptions)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



