<template>
  <div class="app-index">
    <app-header>
      <span slot="left"></span>
      <span slot="title"></span>
      <span slot="right" class="icon icon-search" @click="show"></span>
    </app-header>
    <div class="ai-content" ref="aiContent">
      <tabs-main-blog :app-list="appList" @top="setTop" @bottom="setBottom"/>
    </div>
    <div class="ai-search-panel" v-show="showFlag">
      <div class="ai-header">
        <span class="ai-icon-box"><i class="icon icon-search"></i></span>
        <input class="ai-input" type="text" />
        <a class="ai-cancel" href="javascript:;" @click="hide"></a>
      </div>
      <div class="ai-recommend">
        <div>
          <p class="ai-title"></p>
          <div class="ai-content2">
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import AppHeader from '@/components/AppHeader'
import TabsMainBlog from '@/components/TabsMainBlog'
import axios from 'axios'

export default {
  name: 'appIndex',
  components: {
    AppHeader,
    TabsMainBlog
  },
  data () {
    return {
      appList: {},
      showFlag: false
    }
  },
  created () {
    this.$_showAppNav()
    axios.get('static/mocks/texts-main.json').then((res) => {
      this.appList = res.data
    })
  },
  methods: {
    ...mapActions({
      $_showAppNav: 'showAppNav'
    }),
    show () {
      this.showFlag = true
    },
    hide () {
      this.showFlag = false
    },
    setTop () {
      this.$refs.aiContent.style.zIndex = '990'
    },
    setBottom () {
      this.$refs.aiContent.style.zIndex = ''
    }
  }
}
</script>

<style lang="scss">
@import "../../assets/scss/md";
.app-index {
  position: absolute;
  width: 100%;
  height: 100%;
  .ai-content {
    position: absolute;
    top: 0;
    left: 0;
    padding: 48px 0 58px 0;
    width: 100%;
    height: 100%;
  }
  .ai-search-panel {
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 10px;
    width: 100%;
    height: 100%;
    background: $bgColor;
    z-index: $zIndexOperation;
    .ai-header {
      display: flex;
      width: 100%;
      height: 38px;
      line-height: 38px;
      border-bottom: 1px solid #ccc;
      .ai-icon-box {
        @include centerH();
        width: 10%;
      }
      .ai-input {
        flex: 1;
        outline: none;
      }
      .ai-cancel {
        width: 10%;
      }
    }
    .ai-recommend {
      position: absolute;
      top: 0;
      left: 0;
      padding: 38px 0;
      width: 100%;
      height: 100%;
      @include centerH();
      z-index: -1;
      text-align: center;
      .ai-title {
        margin: 0 auto;
        width: 40%;
        height: 38px;
        line-height: 38px;
        color: #ddd;
        border-bottom: 1px solid #ddd;
      }
      .ai-content2 {
        margin-top: 30px;
        color: $fontColor;
        line-height: 38px;
      }
    }
  }
}
</style>



