<template>
  <div class="app-texts">
    <app-header :is-transparent="isTransparent">
      <span slot="left" class="icon icon-back" @click="back"></span>
      <span slot="title"></span>
      <span slot="right"></span>
    </app-header>
    <swiper class="texts-swiper" :options="textsSwiperOption" ref="textsSwiper">
      <swiper-slide>
        <div class="texts-cover"></div>
        <div class="texts-box">
        <h1 class="texts-title">{{blog.title}}</h1>
        <p class="texts-author"><span class="name"></span></p>
        <div class="texts-content"><br><br><br><br><img><br><br><br><br><br><br><img><br><br><br><br><br><br><br><br><img><br><br><br><br><br><br><img><br><br></div>
        <p class="texts-count">
          <span class="icon icon-collection"><i class="num"></i></span>
          <span class="icon icon-share"><i class="num"></i></span>
        </p>
        <div class="texts-about">
          <h2 class="texts-title-s"></h2>
          <div class="text-about-content">
            <div class="avatar"></div>
            <div class="intro">
              <p class="name"></p>
              <p class="tags"></p>
            </div>
            <div class="follow">
              <app-button-follow :isFriend="false"/>
            </div>
          </div>
        </div>
        <div class="texts-articles-list" v-if="textsType === 0">
          <h2 class="texts-title-s"></h2>
          <ul class="texts-articles-wrap">
            <li class="texts-articles-item">
              <div class="avatar"></div>
              <div class="content">
                <div class="title"></div>
                <p class="info">
                  <span class="date">916</span>
                  <span class="count">
                    <i class="icon icon-collection">86</i>
                    <i class="icon icon-share">86</i>
                  </span>
                </p>
              </div>
            </li>
            <li class="texts-articles-item">
              <div class="avatar"></div>
              <div class="content">
                <div class="title"></div>
                <p class="info">
                  <span class="date">826</span>
                  <span class="count">
                    <i class="icon icon-collection">360</i>
                    <i class="icon icon-share">86</i>
                  </span>
                </p>
              </div>
            </li>
          </ul>
        </div>
        <div class="texts-images-list" v-else-if="textsType === 1">
          <h2 class="texts-title-s"></h2>
        </div>
        <div class="texts-videos-list" v-else-if="textsType === 2">
          <h2 class="texts-title-s"></h2>
        </div>
        <div class="texts-empty" v-else>
           :D
        </div>
        <div class="texts-comments-list">
            <h2 class="texts-title-s"></h2>
            <ul class="texts-comments-wrap">
              <li class="texts-comments-item">
                <div class="avatar"></div>
                <div class="content">
                  <p class="info">
                    <span class="name"></span>
                    <span class="date">07/09/16</span>
                  </p>
                  <div class="text"></div>
                  <p class="count">
                    <span class="icon icon-comment"></span>
                    <span class="icon icon-thumbup"></span>
                  </p>
                </div>
              </li>
              <li class="texts-comments-item">
                <div class="avatar"></div>
                <div class="content">
                  <p class="info">
                    <span class="name"></span>
                    <span class="date">07/09/16</span>
                  </p>
                  <div class="text"></div>
                  <p class="count">
                    <span class="icon icon-comment"></span>
                    <span class="icon icon-thumbup"></span>
                  </p>
                </div>
              </li>
            </ul>
        </div>
      </div>
      </swiper-slide>
    </swiper>
    <div class="texts-footer">
      <input class="input" type="text" placeholder="">
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import AppHeader from '@/components/AppHeader'
import AppButtonFollow from '@/components/AppButtonFollow'
import axios from 'axios'
export default {
  name: 'appTexts',
  components: {
    AppHeader,
    AppButtonFollow
  },
  data () {
    const self = this
    return {
      blog: '',
      textsType: 3,
      isTransparent: true,
      textsSwiperOption: {
        /* eslint-disable */
        // scrollbar: {
        //   el: '.swiper-friends-scrollbar',
        //   hide: true
        // },
        on: {
          touchMove: function() {
            if (this.translate <= -48 && self.isTransparent) {
              self.isTransparent = false
            } else if (this.translate > -48 && !self.isTransparent) {
              self.isTransparent = true
            }
          }
        }
        /* eslint-enable */
      }
    }
  },
  computed: {
    textsSwiper () {
      return this.$refs.textsSwiper.swiper
    }
  },
  created () {
    this.$_hideAppNav()
    axios.get('static/mocks/texts-blog.json').then((res) => {
      this.blog = res.data.blog
      this.textsType = this.blog.type
    })
  },
  methods: {
    ...mapActions({
      $_hideAppNav: 'hideAppNav'
    }),
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss">
@import "../../assets/scss/md";
.app-texts {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $slideBgColor;
  z-index: $zIndexPage;
  .texts-swiper {
    padding-bottom: 48px;
    width: 100%;
    height: 100%;
    .texts-cover {
      padding-top: 70%;
      width: 100%;
      height: 0;
      overflow: hidden;
    }
    .texts-box {
      padding: 20px;
      background: $bgColor;
      .texts-title {
        margin-bottom: 20px;
        font-size: 16px;
        line-height: 24px;
        font-weight: 700;
      }
      .texts-author {
        font-size: 14px;
        color: #aaa;
      }
      .texts-content {
        margin-top: 10px;
        line-height: 26px;
        color: #333;
        font-size: 14px;
        img {
          padding-top: 60%;
          width: 100%;
          height: 0;
          background: #ccc;
        }
      }
      .texts-count {
        @include centerH();
        .icon {
          margin: 30px 60px;
          width: 30px;
          height: 30px;
        }
      }
      .texts-about {
        margin-bottom: 38px;
        .text-about-content {
          display: flex;
          padding: 15px 0;
          .avatar {
            width: 30px;
            height: 30px;
            background: #ccc;
            border-radius: 100%;
          }
          .intro {
            flex: 1;
            padding: 0 10px;
            .name {
              font-size: 16px;
              height: 40px;
              line-height: 40px;
            }
            .tags {
              font-size: 14px;
              color: #ccc;
            }
          }
        }
      }
      .texts-articles-list {
        margin-bottom: 38px;
        .texts-articles-wrap {
          .texts-articles-item {
            display: flex;
            padding: 10px 0;
            & + .texts-articles-item {
              border-top: 1px solid #eee;
            }
            .avatar {
              width: 80px;
              height: 80px;
              background: #ccc;
              .img {
                width: 100%;
                height: 100%;
              }
            }
            .content {
              position: relative;
              flex: 1;
              padding-left: 10px;
              .title {
                font-size: 14px;
                line-height: 20px;
              }
              .info {
                position: absolute;
                display: flex;
                left: 0;
                bottom: 0;
                padding: 0 10px;
                width: 100%;
                justify-content: space-between;
                font-size: 12px;
                color: #ccc;
              }
            }
          }
        }
      }
      .texts-comments-list {
        margin-bottom: 38px;
        .texts-comments-wrap {
          .texts-comments-item {
            display: flex;
            padding: 10px 0;
            & + .texts-comments-item {
              border-top: 1px solid #eee;
            }
            .avatar {
              width: 28px;
              height: 28px;
              background: #ccc;
              border-radius: 100%;
              .img {
                width: 100%;
                height: 100%;
              }
            }
            .content {
              position: relative;
              flex: 1;
              padding-left: 10px;
              .info {
                display: flex;
                height: 28px;
                line-height: 28px;
                width: 100%;
                justify-content: space-between;
                font-size: 12px;
                color: #ccc;
              }
              .text {
                font-size: 14px;
                line-height: 15px;
              }
              .count {
                margin-top: 15px;
                text-align: right;
              }
            }
          }
        }
      }
      .texts-empty {
        padding-bottom: 38px;
        text-align: center;
        font-size: 14px;
        color: #ccc;
      }
      .texts-title-s {
        width: 70px;
        height: 25px;
        line-height: 25px;
        border-bottom: 2px solid #333;
        white-space: pre;
      }
    }
  }
  .texts-footer {
    display: flex;
    position: fixed;
    padding: 8px 12px;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 48px;
    background: $bgColor;
    z-index: $zIndexNav;
    .input {
      flex: 1;
      margin-right: 10px;
      padding: 6px 12px;
      font-size: 14px;
      line-height: 1.42857143;
      color: #555;
      background-color: #f7f7f7;
      background-image: none;
      border: 1px solid #f3f3f3;
      border-radius: 4px;
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
      outline: none;
    }
  }
}
</style>



